:root {
  --clr-background: rgb(18, 18, 19);
  --clr-dark-grey: rgb(58, 58, 60);
  --clr-light-grey: rgb(129, 131, 132);
  --clr-green: rgb(97, 139, 85);
  --clr-yellow: rgb(177, 159, 76);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  background-color: var(--clr-background);
  width: 100%;
  height: 100%;
  position: absolute;
  font-family: "Open Sans", sans-serif;
}

.hint-button {
  background-color: black;
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.244);
  border-radius: 5px;  /* Rounded edges */
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.hint-button:hover {
  background-color: white;
  color: black;
  transform: scale(1.05);  /* Slight scale effect on hover */
}

.hint-button:focus {
  outline: none;  /* Remove the default focus outline */
}


/* Popup background */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100%;
  max-height: 95%;
  background-color: rgba(0, 0, 0, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3%;
  z-index: 1000;
  transform: scale(0.9);
}

/* Popup content */
.popup-content {
  background-color: white;
  border-radius: 10px;
  text-align: left;
  width: 350px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Circular image style */
.popup-image {
  width: 80px;
  height: 80px;
  border-radius: 50%; /* Circular crop */
  object-fit: cover; /* Ensure the image is cropped well */
  margin-left: 20px;
}

/* Text content container */
.popup-text {
  padding: 0px 15px 10px 15px;
  margin-bottom: 20px; /* Space between text and button */
}

/* Popup header text */
.popup-header {
  font-size: 14px;
  font-weight: bold;
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Popup body text */
.popup-body {
  font-size: 10px;
  min-width: 100%;
}

/* Button style */
.popup-content button {
  padding: 10px;
  background-color: #000000;
  margin-bottom: 20px;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.popup-content button:hover {
  background-color: #0056b3;
}

.title {
  color: white;
  font-size: 1.75rem;
  margin: 5px;
  font-family: "Roboto Slab", serif;
}

.title-bar {
  border-bottom: 2px solid var(--clr-dark-grey);
  width: 100%;
}

#alert-box {
  position: absolute;
  top: 9vh;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  z-index: 1;
}

.alert {
  padding: 0.5rem;
  margin: 0.5rem;
  width: fit-content;
  background-color: white;
  border-radius: 5px;
}

.alert-fade {
  animation: fade-out 2s ease-out 2s forwards;
}

@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.invalid-guess {
  animation: tile-shake 0.25s ease-in-out;
}

@keyframes tile-shake {
  0% {
    transform: translateX(5px);
  }
  50% {
    transform: translateX(-10px);
  }
  100% {
    transform: translateX(5px);
  }
}

.winning-guess {
  animation: tile-jump 0.25s ease-in-out;
}

@keyframes tile-jump {
  0% {
    transform: translateY(5px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(5px);
  }
}

/* in case this animation will be added in the future! */
.new-guess {
  animation: tile-spin 0.5s ease-in-out;
}

@keyframes tile-spin {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}

.puzzle-board {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  position: absolute;
  outline: none;
}

.puzzle-box {
  height: 60px;
  width: 60px;
  border: 2px solid var(--clr-dark-grey);
  margin: 3px;
  font: inherit;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-size: 2rem;
}

.puzzle-line {
  display: flex;
}

.keyboard-row {
  display: flex;
  justify-content: center;
  margin: 0 auto 8px;
}

.keyboard-key {
  height: 58px;
  width: 43px;
  margin: 0 6px 0 0;
  background-color: var(--clr-light-grey);
  color: white;
  font-size: 2rem;
  border: 0;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font: inherit;
  cursor: pointer;
}

.special-key {
  width: 75px;
}

.key-green,
.puzzle-box-green {
  background-color: var(--clr-green);
  border-color: var(--clr-green);
}

.key-yellow,
.puzzle-box-yellow {
  background-color: var(--clr-yellow);
  border-color: var(--clr-yellow);
}

.key-grey,
.puzzle-box-grey {
  background-color: var(--clr-dark-grey);
  border-color: var(--clr-dark-grey);
}

@media only screen and (max-width: 600px) {
  .keyboard-key {
    height: clamp(40px, 10vw, 58px);
    width: clamp(25px, 7vw, 43px);
    font-size: 0.9em;
  }
  .special-key {
    width: clamp(57px, 12vw, 75px);
  }
  .puzzle-box {
    height: clamp(40px, 13vw, 60px);
    width: clamp(40px, 13vw, 60px);
  }
  .title {
    font-size: 1.5rem;
  }
}
